import React from 'react'
import Button from '@material-ui/core/Button';

class Form extends React.Component {
  constructor(props) {
    super(props)

    this.initialState = {
      name: '',
      job: '',
    }

    this.state = this.initialState
  }

  handleChange = event => {
    const { name, value } = event.target

    this.setState({
      [name]: value,
    })
  }

  submitForm = () => {
    this.props.handleSubmit(this.state)  // submit {name:x, job:x} from form state
    this.setState(this.initialState)
  }

  render() {
    const { name, job } = this.state

    return (
    <form>
      <label>Name</label>
      <input
        type="text"
        name="name"
        value={name}
        onChange={this.handleChange} />
      <label>Job</label>
      <input
        type="text"
        name="job"
        value={job}
        onChange={this.handleChange} />
      <Button variant="contained" color="primary" onClick={this.submitForm}>
        SUBMIT
      </Button>
    </form>
    )
  }
}

export default Form